import { useTranslation } from "gatsby-plugin-react-i18next";

import { EditionData, EventDates } from "../data";

const useEventDates = (editionData: EditionData): EventDates => {
  const { t } = useTranslation();

  const eventIntervals = editionData.eventIntervals;

  return {
    conferenceRegistration:
      `${eventIntervals.conferenceRegistration[0]} ${t("months.february")} - ` +
      `${eventIntervals.conferenceRegistration[1]} ${t("months.march")}`,
    hackathonRegistration:
      `${eventIntervals.hackathonRegistration[0]} ${t("months.february")} - ` +
      `${eventIntervals.hackathonRegistration[1]} ${t("months.march")}`,
    speedDating: `${eventIntervals.speedDating} ${t("months.march")}`,
    workshops: `${eventIntervals.workshops} ${t("months.march")}`,
    conference: `${eventIntervals.conference} ${t("months.march")}`,
    hackathon: `${eventIntervals.hackathon} ${t("months.march")}`,
    presentations: `${eventIntervals.presentations} ${t("months.march")}`,
  };
};

export default useEventDates;
