import { useTranslation } from "gatsby-plugin-react-i18next";

import { EditionData } from "../data";
import useEventDates from "../hooks/useEventDates";

import RegisterButton from "./general/registerButton";

import * as styles from "./hackathon.module.css";
import React from "react";

type HackathonProps = {
  editionData: EditionData;
};

type HackathonDetails = {
  title: string;
  content: string;
  extra_details?: {
    text: string;
    link: string;
  };
};

const Hackathon: React.FC<HackathonProps> = ({ editionData }) => {
  const { t } = useTranslation();

  const { hackathon: date } = useEventDates(editionData);
  const details = t("hackathonDetails", {
    returnObjects: true,
  }) as HackathonDetails[];

  return (
    <div className={styles.hackathon} id={"Hackathon"}>
      <div className={styles.container}>
        <h2>{date}</h2>
        <h1>{t("hackathonTitle")}</h1>

        <div className={styles.info}>
          {details.map((item, index) => (
            <div key={"Hackathon" + index}>
              <p className={`p ${styles.question}`}>{t(item.title)}</p>
              <p className={"p"}>{t(item.content)}</p>
            </div>
          ))}
          <>
            <a
              className={"p a"}
              href={"https://nlp.unibuc.ro/"}
              target={"_blank"}
              rel={"noreferrer"}
            >
              HLTRC = Human Language Technologies Research Center
            </a>
          </>
        </div>

        <RegisterButton />
      </div>
    </div>
  );
};

export default Hackathon;
