import { useMemo } from "react";

import { Edition, EditionData } from "../data";

import edition2022Data from "../../data/2022.json";
import edition2023Data from "../../data/2023.json";

const useEditionData = (edition: Edition): EditionData =>
  useMemo(() => {
    switch (edition) {
      case 2022:
        return edition2022Data as EditionData;
      case 2023:
        return edition2023Data as EditionData;
      default:
        throw `unknown edition ${edition}`;
    }
  }, [edition]);

export default useEditionData;
