import React from "react";
import { Button } from "react-bootstrap";

import { useTranslation } from "gatsby-plugin-react-i18next";
import { graphql, PageProps } from "gatsby";

import Layout from "../components/layout";
import Seo from "../components/seo";
import Hackathon from "../components/hackathon";
import Partners from "../components/pages/partners";

import RegisterButton from "../components/general/registerButton";
import useEditionData from "../hooks/useEditionData";

import * as styles from "./index.module.css";

const Home: React.FC = () => {
  const { t } = useTranslation();

  return (
    <div className={styles.home}>
      <div className={styles.box}>
        <h1 className={styles.title}>
          NITRO <br />
          LANGUAGE <br />
          PROCESSING <br />
          HACKATHON
        </h1>
        <h3 className={styles.subtitle}>{t("motto")}</h3>
        <div className={styles.buttons}>
          <RegisterButton className={styles.button} />
          <Button className={`buttonLink ${styles.button}`} href={"#Hackathon"}>
            {t("details")}
          </Button>
        </div>
      </div>
    </div>
  );
};

const Prizes: React.FC = () => {
  const { t } = useTranslation();

  return (
    <div className={`page ${styles.prizes}`}>
      <h1 className={styles.title}>{t("prizes")}</h1>
      <h2 className={styles.subtitle}>{t("tba")}</h2>
    </div>
  );
};

const IndexPage: React.FC<PageProps> = ({ location }) => {
  const { t } = useTranslation();
  const editionData = useEditionData(2023);

  return (
    <Layout>
      <Seo
        title={t("title")}
        description={t("description")}
        keywords={["hackathon", "NLP", "students"]}
        pathname={location.pathname}
      />
      <Home />
      <Hackathon editionData={editionData} />
      <Prizes />
      <Partners edition={2023} />
    </Layout>
  );
};

export default IndexPage;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(
      filter: {
        ns: { in: ["common", "index", "hackathon", "prizes", "partners"] }
        language: { eq: $language }
      }
    ) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
